.profile__status {
  color: #fff;
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.22;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 686px) {
  .profile__status {
    font-size: 14px;
    line-height: 1.21;
    text-align: center;
  }
}